@import '../../styles/common.css';

.container {
  width: 100%;
  height: 100vh;
  background-color: #fff;
}

.background,
.foreground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.background {
  height: 50%;
}

.content {
  max-width: 750px;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: end;
  margin: auto;
  padding: 10px;
  box-sizing: border-box;
}

.content .copy {
  flex: 2;
  @media (max-width: 500px) and (max-height: 950px) {
    margin-top: -33%;
  }
}

.content .copyPadding {
  flex: 1;
}

.foreground {
  z-index: 2;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.header1 {
  font-size: 3rem;
  @media (max-width: 450px) {
    font-size: 2rem;
  }
}

.header2 {
  font-size: 1.5rem;
  @media (max-width: 450px) {
    font-size: 0.75rem;
    font-weight: bold;
    line-height: 1.5;
  }
}

.listBullet {
  font-size: 66%;
  margin-right: 0.25rem;
}

.navigation {
  display: flex;
  flex-direction: column;
  @media (max-height: 400px) {
    flex-direction: row;
  }
}

.navigation > ul {
  flex: 1;
  margin-top: 1rem;
}

.navigation > ul a {
  display: flex;
  flex-direction: row;
  align-items: center;

  @media (max-width: 500px) and (max-height: 950px) {
    border: 1px solid #000000;
    padding: 0.25rem 0.5rem;
    box-sizing: border-box;
    margin-bottom: 0.25rem;
  }
}

.responsiveMessage {
  line-height: 1.5;
  display: none;
  border: 2px solid #000000;
  border-radius: 0.3rem;
  padding: 0.25rem 0.5rem;
  font-size: 80%;
  box-sizing: border-box;
  margin-top: 0.5rem;

  @media only screen and (max-device-width: 640px) {
    display: block;
  }
}

.responsiveMessage ul {
  box-sizing: border-box;

  padding-left: 0.5rem;
}

.responsiveMessage li {
  margin-left: 0.5rem;
  list-style: circle;
}
